import logo from "./2.svg";
import TypeAnimation from "react-type-animation";
import Images from "./images";
import Badge from "react-bootstrap/Badge";
import Mycaraousel from "./mycaraousel";

function Home() {
  return (
    <>
      <div className="logodiv">
        <div className="headingcontainer">
          <TypeAnimation
            cursor={true}
            sequence={[
              "Our aim is to make smart agriculture available to each and every indian farmer",
              10000,
              "",
            ]}
            wrapper="h1"
            repeat={Infinity}
          />
          <h2 style={{ width: "100%" }}>
            One Step toward making Agriculture Smart......
          </h2>
        </div>
        <img src={logo} className="logoimg"></img>
      </div>
      <Mycaraousel />
      <h1 style={{ marginTop: "10px" }}>
        <Badge bg="secondary">Recent Articles on Smart Agriculture</Badge>
      </h1>
      <Images />
    </>
  );
}

export default Home;
