function Dashboard() {
  const [key, setKey] = useState("Visualize Data");

  return (
    <Tabs
      id="controlled-tab-example"
      activeKey={key}
      onSelect={(k) => setKey(k)}
      className="mb-3"
    >
      <Tab eventKey="Visualize Data" title="Visualize Data">
        <div className="charts">
          <h1 style={{ width: "100%" }}>
            <Badge bg="secondary">Sensor Data Visualization</Badge>
          </h1>
          <Mytable />
          <div className="mychart">
            <VictoryChart
              animate={{
                duration: 2000,
                onLoad: { duration: 1000 },
              }}
              theme={VictoryTheme.material}
            >
              <VictoryLine
                style={{
                  data: { stroke: "#c43a31" },
                  parent: { border: "1px solid #ccc" },
                }}
                data={soilMoistureData}
              />
              <VictoryAxis
                label="time"
                style={{
                  axisLabel: { padding: 30 },
                }}
              />
              <VictoryAxis
                dependentAxis
                label="soil temperature"
                style={{
                  axisLabel: { padding: 40 },
                }}
              />
            </VictoryChart>
          </div>
          <div className="mychart">
            <VictoryChart
              animate={{
                duration: 2000,
                onLoad: { duration: 1000 },
              }}
              theme={VictoryTheme.material}
            >
              <VictoryArea data={soilMoistureData} />
              <VictoryAxis
                label="time"
                style={{
                  axisLabel: { padding: 30 },
                }}
              />
              <VictoryAxis
                dependentAxis
                label="soil humidity"
                style={{
                  axisLabel: { padding: 40 },
                }}
              />
            </VictoryChart>
          </div>
          <div className="mychart">
            <VictoryChart
              theme={VictoryTheme.material}
              domainPadding={10}
              animate={{
                duration: 2000,
                onLoad: { duration: 1000 },
              }}
            >
              <VictoryBar
                style={{ data: { fill: "#c43a31" } }}
                data={soilMoistureData}
              />
              <VictoryAxis
                label="time"
                style={{
                  axisLabel: { padding: 30 },
                }}
              />
              <VictoryAxis
                dependentAxis
                label="soil Moisture"
                style={{
                  axisLabel: { padding: 40 },
                }}
              />
            </VictoryChart>
          </div>
        </div>
      </Tab>
      <Tab eventKey="Schedule Irrigation" title="Schedule Irrigation">
        <Mytime />
      </Tab>
    </Tabs>
  );
}
