import Table from "react-bootstrap/Table";

export default function Mytable() {
  return (
    <Table className="mytable" striped bordered hover variant="dark">
      <thead>
        <tr>
          <th>#</th>
          <th>Soil Moisture</th>
          <th>Temperature</th>
          <th>Humidity</th>
          <th>Irrigation status</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>1</td>
          <td>67</td>
          <td>36</td>
          <td>25</td>
          <td>ON</td>
        </tr>
        <tr>
          <td>2</td>
          <td>56</td>
          <td>42</td>
          <td>22</td>
          <td>OFF</td>
        </tr>
        <tr>
          <td>3</td>
          <td>45</td>
          <td>43</td>
          <td>56</td>
          <td>OFF</td>
        </tr>
      </tbody>
    </Table>
  );
}
